<template>
<div class="bg-portafolio col-12 ">
  <h2 class="text-white" >Portafolio</h2>
  <div class="portafolio text-white" >
    <carousel
      @next="next"
      @prev="prev" >
        <carousel-slide v-for="(slide,index) in slides" 
        :key="slide" :index="index"
        :visibleSlide ="visibleSlide"
        :direction = "direction" >
        <img :src="slide" >        
        </carousel-slide>
    </carousel>
  
  </div>
</div>
</template>

<script>
import Carousel from "@/components/carousel/Carousel.vue"
import CarouselSlide from "@/components/carousel/CarouselSlide.vue"

import {ref,computed} from "vue"

//import {ref} from "vue"

export default {
  name:"Portafolio",
  components:{
    Carousel,
    CarouselSlide
  },
  setup() {

    const slides = ref([
      require("@/assets/image/img01.jpg"),
      require("@/assets/image/img02.jpg"),
      require("@/assets/image/img03.jpg"),
      require("@/assets/image/img04.jpg"),
      require("@/assets/image/img05.jpg")
    ])

   const visibleSlide =   ref(0);
   const direction =   ref('left');

  const slidesLen = computed(()=>{
     return slides.value.length;
   })

     const  next = () => {
      if(visibleSlide.value >= slidesLen.value-1){
        visibleSlide.value = 0;
      }else{
        visibleSlide.value++;
      }
      direction.value = "left";
    }
    const prev = () => {
      if(visibleSlide.value <= 0){
        visibleSlide.value = slidesLen.value-1;
      }else{
        visibleSlide.value--;
      }
        direction.value = "right";
   }

    return{slides, visibleSlide,next,prev}

  }
}
</script>

<style scoped>
.bg-portafolio{
  background: -webkit-linear-gradient(to top, #0575e6, #021b79); /*Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #0575e6, #021b79);
}
.portafolio{
  display: flex;
  justify-content: center;
}

img{
  width: 55%;
  height: 85%;
}

@media (max-width: 576px) { 
img{
  width: 75%;
  height: 80%;
}
}

</style>